<template>
  <div class="crossTab1">
    <div class="tab-list">
      <div class="tab-item">
        BroadCast Channel 接收的消息:{{ BroadCastMessage }}
      </div>
      <div class="tab-item">
        LocalStorage window.onstorage 接收的消息:{{ localMessage }}
      </div>
      <div class="tab-item">
        postMessage window.message 接收的消息:{{ postMessage }}
      </div>
      <div class="tab-item">
        cookie循环(setInterval)接收的消息:{{ cookieMessage }}
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref, onMounted } from "vue";
const BroadCastMessage = ref("");
const localMessage = ref("");
const postMessage = ref("");
const cookieMessage = ref("");
onMounted(() => {
  getBroadCastMessage();
  getLocalMessage();
  getMessage();
  getCookieMessage();
});
const getBroadCastMessage = () => {
  let channel = new BroadcastChannel("myChannel");
  channel.onmessage = (e) => {
    BroadCastMessage.value = e.data;
  };
};
const getLocalMessage = () => {
  getLocal();
  window.addEventListener("storage", (e) => {
    getLocal();
  });
};
const getLocal = () => {
  let name = localStorage.getItem("personalName");
  let age = localStorage.getItem("personalAge");
  localMessage.value = "姓名：" + name + "，年龄：" + age;
};
const getMessage = () => {
  window.addEventListener("message", (e) => {
    console.log(e);
    postMessage.value = e.data;
  });
};
const getCookieMessage = () => {
  cookieMessage.value = document.cookie;
  setInterval(() => {
    if (cookieMessage.value != document.cookie) {
      cookieMessage.value = document.cookie;
      console.log(cookieMessage.value);
    }
  }, 1000);
};
</script>
<style lang="scss" scoped>
.crossTab1 {
  .tab-list {
    display: flex;
    align-items: flex-start;
    .tab-item {
      width: 200px;
      border-radius: 8px;
      padding: 10px;
      border: 2px solid #ccc;
      margin: 10px;
    }
  }
}
</style>